/* PRODUCT ROWS */

#productsImagesRow1 {
    /* height: 25vw; */
    padding-bottom: 2vw;
    width: 90%;
    margin: 0 auto;
    margin-top: 5vw;
    align-content: center;
}

#productsImagesRow2 {
    /* height: 25vw; */
    padding-bottom: 2vw;
    width: 90%;
    margin: 0 auto;
    margin-top: 1vw;
    align-content: center;
}

.productsImagePanel {
    width: 22%;
    height: 100% !important;
    margin: 1vw;
}

.productsImage {
    width: 100%;
    margin: 0 auto !important;
    height: auto;
    cursor: pointer;
}

.productListingDetailsCol {
    text-align: center;
}

.productListingImageCol {
    margin: 0 auto !important;
}

/* PRODUCT MODALS */

.productsImageModal {
    width: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: auto;
}

.productsImageHeader {
    padding: 1vw !important;
}

.productsImageHeader .rs-panel-heading {
    padding: 1vw !important;
    font-size: 2vw;
    text-align: center;
}

.productsImageHeader .rs-panel-body {
    display: none;
}

.rs-dropdown-menu {
    max-height: 50vh;
    overflow: scroll;
}

#productsListingName {
    text-align: center;
    font-size: 3vw;
}

.productsListingPrice, .productsListingIngredients {
    margin-top: 1vw;
    font-size: 1vw;
    font-weight: bolder;
}

.productsTitleRow {
    width: 100%;
    display: inline-block !important;
}

.productsTitle {
    font-size: 5vw;
    float: left;
    width: 30%;
    margin-left: 15vw;
    font-family: 'Didot';
}

/* SEARCH BAR */

#adminProductsSearchBarForm {
    float: right;
    width: 30%;
    margin-right: 10vw;
    margin-top: 1vw;
}

#adminProductsSearchBarFormGroup .rs-form-control-wrapper {
    width: 70%;
}

.searchIcon {
    margin-left: 1vw;
}

/* LOADER */

#productsLoader {
    margin-top: 10vw;
    height: 10vw;
    position: relative;
    top: 0;
}



