/* Portrait */
@media only screen 
and (min-device-width: 320px) 
and (max-device-width: 568px)
and (-webkit-min-device-pixel-ratio: 2)
and (orientation: portrait) {

    .productsTitle {
        width: 20vw;
        margin-top: 1vw;
        margin-left: 15vw;
        font-size: 6vw;
    }

    /* SEARCH BAR */

    #adminProductsSearchBarForm {
        float: left;
        width: 65vw;
        margin-right: 0vw;
        font-size: 3.5vw;
    }

    #adminProductsSearchBarForm.rs-form-control-wrapper {
        width: 100%;
    }

    #searchEntry {
        max-width: 100%;
        font-size: 3.2vw;
    }

    /* PRODUCT ROWS */
    .productsImagePanel {
        width: 45%;
        margin-left: 2.5%;
    }

    .productsImageHeader .rs-panel-heading {
        font-size: 4vw;
    }

    /* PRODUCT MODALS */
    .rs-modal-content {
        max-width: 100vw;
    }
    #productsListingName {
        font-size: 5vw;
    }

    .productsImageModal {
        width: 40vw;

        height: auto;
    }
    
    .productListingDetailsCol {
        max-width: 100%;
    }

    .productListingDetailsCol p {
        font-size: 5vw;
    }

    .productListingDetailsCol h5 {
        font-size: 5vw;
    }


}

